// elements index page
#elements-list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  width: 100%;
  font-size: 120%;

  a {
    margin: 20px 0px;
  }
}

.element-card {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 15vh;
  background: 
    linear-gradient(
      to left,
      rgba(0, 0, 0, 0),
      rgba(0, 0, 0, .6),
      rgba(0, 0, 0, 0)
    );

  display:flex;
  align-items: center;
  justify-content: center;

  h1 {
    color: white;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
    padding-left: 20px;
    text-align: center;
    z-index: 1;
  }

  img {
    position: absolute;
    top: -40px;
    opacity: .6;
    width: 100%;
    transition: opacity .3s;
  }
}

.element-card:hover > img {
  opacity: 1;
}

// elements show and edit page
#show-element, #edit-element {
  width: 90vw;
  margin: 0 5vw;
  background-color: rgba(221, 229, 114, .7);
  padding: 10px;
  border-radius: 20px;
  border: 5px solid #242700;

  h4 {
    margin: 0px;
  }

  .elements-section {
    background-color: #F7FCBA;
    margin: 10px;
    padding: 5px 10px;
    border-radius: 10px;
  }

  .instructions {
    margin: 15px 0px;
  }
  
  input[type=submit] {
    -moz-box-shadow: inset -3px -3px 10px #130C0D;
    -webkit-box-shadow: inset -3px -3px 10px #130C0D;
    box-shadow: inset -3px -3px 10px #130C0D;
    background-color: #73452F;
    color: white;
    border-radius: 15px;
    font-style: italic;
    font-weight: bold;
    padding: 10px 30px;
    margin: 5px;
  }

  #periodic-ul li:nth-child(n+2){
    display:none;
 }

  .instructions-text {
    white-space: pre-line;
  }
}